import React, { useEffect, useState } from 'react'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import Persons from './components/Persons'
import Notification from './components/Notification'

import phoneBookService from './services/phonebook'

const App = () => {
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ filterName, setFilterName ] = useState('')
  const [ persons, setPersons ] = useState([])
  const [ filterPersons, setFilterPersons ] = useState([])
  const [ message, setMessage ] = useState({})

  useEffect(() => {
    phoneBookService
      .getAll()
      .then(initialPerson => {
        setPersons(initialPerson)
        setFilterPersons(initialPerson)
      })
  }, [])

  const addSubmitHanlder = (event) => {
    event.preventDefault()

    const checkName = persons.find(e => e.name === newName)

    if (newName.trim() === '') {
      alert(`Please add name`)
      return
    }

    if (newNumber.trim() === '') {
      alert(`Please add number`)
      return
    }

    if (checkName !== undefined) {
      updatePhone(checkName)
      return
    }

    createPerson()
  }

  const nameHandler = (event) => {
    setNewName(event.target.value)
  }

  const numberHandler = (event) => {
    setNewNumber(event.target.value)
  }

  const filterNameHandler = (e) => {
    const filter = persons.filter(p => p.name.toLowerCase().includes(e.target.value.toLowerCase()))
    setFilterPersons(filter)
    setFilterName(e.target.value)
    setMessage({})
  }

  const deletePerson = (person) => {
    const personName = person.name
    if (window.confirm(`Delete ${personName} ?`)) {
        phoneBookService
          .remove(person.id)
          .then(response => {
              setPersons(persons.filter(p => p.id !== person.id))
              setFilterPersons(persons.filter(p => p.id !== person.id))
              setMessage({type: 'error', message: `Deleted ${personName}`})
          })
          .catch(error => {
            setPersons(persons.filter(p => p.id !== person.id))
            setFilterPersons(persons.filter(p => p.id !== person.id))
            setMessage({type: 'error', message: `Information of ${personName} has already been removed from server`})
          })
    }
  }

  const updatePhone = (person) => {
    const responseText = `${person.name} is already added to phonebook, replace the old number with a new one?`

    if ( window.confirm(responseText)) {
      phoneBookService
        .update(person.id, {'name': newName, 'number': newNumber})
        .then(returnedPerson => {
          setPersons(persons.map(p => p === person ? returnedPerson : p))
          setFilterPersons(persons.map(p => p === person ? returnedPerson : p))
          setMessage({type: 'success', message: `Updated phone number of ${newName}`})
        })
    }
  }

  const createPerson = () => {
    phoneBookService
      .create({'name': newName, 'number': newNumber})
      .then(returnedPerson => {
        setPersons([...persons, returnedPerson])
        setFilterPersons([...persons, returnedPerson])
        setMessage({type: 'success', message: `Added ${newName}`})
        setNewName('')
        setNewNumber('')
        setFilterName('')
      })
  }

  return (
    <div>
      <h2>Phonebook</h2>

      <Notification message={message} />

      <Filter filterName={filterName} filterHandler={filterNameHandler} />

      <h2>add a new</h2>

      <PersonForm
        newName={newName}
        nameHandler={nameHandler}
        newNumber={newNumber}
        numberHandler={numberHandler}
        submitHanlder={addSubmitHanlder}
      />

      <h2>Numbers</h2>
      <Persons persons={filterPersons} deletePerson={deletePerson} />
    </div>
  )
}

export default App